body {
  font-family: 'Segoe UI', Roboto, Geneva, Verdana, sans-serif;
  margin: 0;
  padding: 0;
}

.flex-container {
  display: flex;
  flex-flow: row wrap;
  height: 100%;
}

.content {
  flex: 8;
  padding-left: 30px;
}

a:link {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

nav {
  flex: 1;
  line-height: 1.8;
  padding-top: 30px;
  padding-left: 20px;
  padding-right: 20px;
  font-size: larger;
}

.pageheader {
  padding: 20px;
  background-color: rgb(0, 90, 192);
  color: white;
  letter-spacing: 2px;
}

.current {
  color: green;
}


